/* Variables */
$base-font:		'Lato', sans-serif;
$alt-font:		'Times New Roman', serif;
$ico-font:		'FontAwesome';
$font-size:		1.8rem;

$light:			300;
$normal:		400;
$bold:			700;
$xbold:			800;


/* Document font settings */
html {
	font-family: $base-font;
	font-size: 62.5%; // Defining 1 rem = 10px
	color: $black;
}

body {
    font-family: $base-font;
    font-weight: 400;
    line-height: 1.7;
    font-size: $font-size;
}

a {
	text-decoration: none;
	color: $base;
	&:hover {
		color: $alt;
	}
}