.footer {
	background-color: $gray;
	margin-top: 100px;
	&__row {
		justify-content: center;
		align-items: center;
		&:last-child {
			background-color: #fff;
		}
	}
	&__wrapper {
		display: flex;
		justify-content: space-between;
		align-items: center;
    	padding: 20px 0;
	}
	&__info {
		
	}
	&__nav {
		ul {
			list-style: none;
		}
	}
	&__contact {

	}
	&__zensite {
		display: flex;
		justify-content: space-between;
		align-items: center;
		padding: 15px 0;
		div {
			font-size: 1.6rem;
		}
	}
	&__date {

	}
	&__copyrights {
	    display: flex;
	    justify-content: center;
	    align-items: center;
	    a {
	    	display: flex;
    		padding: 0 10px;
	    }
	}
}
.zensite-logo {
	height: 26px;
	margin-left: 5px;
}

@include media-breakpoint-down( sm ){
	.footer {
		&__zensite {
			div {
				width: 100%;
				font-size: 1.5rem;
				&:first-child {
					text-align: center;
				}
			}
		}
		&__info {
			
		}
		&__wrapper {
			div {
				width: 100%;
				text-align: center;
				margin: 16px 0;
			}
		}
	}
}