.dane { width: 960px; height: 70px; position: relative; top: 5px; }

.danegradient { height: 60px; width: 311px; display: inline-block; background: linear-gradient(#CFD81D, #69B62C); border-radius: 15px; }

.wpcf7-text { height: 58px; width: 309px; border-width: 0px; border-radius: 15px; position: relative; left: 1px; top: 1px; right: 1px; bottom: 1px; color: #808080; padding-left: 25px; padding-top: 15px; font-family: 'Rajdhani', sans-serif; font-size: 2rem; }

.emailgradient { position: relative; left: 10px; height: 60px; width: 311px; display: inline-block; background: linear-gradient(#CFD81D, #69B62C); border-radius: 15px; }

.wpcf7-email { padding-left: 10px; }

.wpcf7-submit { border-color: #BFD220; background: linear-gradient(#D5DD39, #6BB42E); border-width: 0px; border-radius: 15px; height: 58px; width: 313px; float: right; margin-top: -60px; color: whitesmoke; font-weight: 600; font-size: 2rem; font-family: 'Rajdhani', sans-serif; }

.wpcf7-response-output { width: 960px; background: linear-gradient(#D5DD39, #6BB42E); text-align: center; color: whitesmoke; border-width: 0px !important; }

.parent--404 { display: flex; }

.parent--404--inner { margin: 0 auto; text-align: center; }

.error404 .col--1 { flex-wrap: wrap; margin-bottom: 40px; }

.error404 .col--1 h1, .error404 .col--1 h2, .error404 .col--1 h3 { width: 100%; text-align: center; }

.error404 .col--1 h1 { font-size: 10vw; line-height: 1; font-weight: 700; position: relative; z-index: 10; }

@media (max-width: 767px) { .error404 .col--1 h1 { font-size: 12vw; } }

.error404 .col--1 h1:after { content: "ERROR"; position: absolute; z-index: 1; top: -20px; font-size: 120%; left: 0; right: 0; color: rgba(33, 32, 31, 0); opacity: 0.2; text-shadow: 0 0 20px #21201f; }

.error404 .col--1 h2 { font-size: 2rem; line-height: 1; font-weight: 700; }

@media (max-width: 767px) { .error404 .col--1 h2 { font-size: 4vw; } }
