/*===========================================
=            Responsive nav menu            =
===========================================*/
 
 .nav__container{
		position:fixed;
		top:0;
		right: 0;
		left:0;
		display: flex;
    	justify-content: space-between;
    	background-color: #fff;
    	box-shadow: 0 6px 16px -12px #999;
    	z-index: 100;
		.container{
			// display: flex;y
			justify-content: space-between;
			align-items:center;
			flex-wrap: nowrap;
		}
		#m_nav{
			display:none;
		}
		nav{
			display: flex;
    		align-items: center;
			ul{
				margin:0;
				padding:0;
				display: flex;
				justify-content:space-between;
				.menu-item-has-children {
					& > .sub-menu {
						transition: .3s;
						position: absolute;
						display: flex;
						flex-direction: column;
						max-height: 0;
						background-color: #fff;
						overflow: hidden;
						.menu-item-has-children {
							&:hover {
								& > .sub-menu {
									max-height: 300px;
								}
							}
							& > .sub-menu {
								position: absolute;
								left: 100%;
								top: 0;
								display: flex;
								flex-direction: column;
							}
						}
					}
					&:hover {
						& > .sub-menu {
							max-height: 300px;
							overflow: visible;
						}
					}
				}
				li{
					margin:0;
					padding:0;
					cursor:pointer;
					list-style: none;
					position: relative;
					a{
						display: inline-block;
						text-align: center;
						text-decoration: none;
						color: #333;						// color of nav items
					}
				}
			}
		}
	}
@include media-breakpoint-up( md ){
	.nav__container{
		// position:relative;
		#mobile-hamburger{
			display:none;
		}
		nav{
			// float:right;
			ul li a{
				padding: 10px 10px;
			}
		}
	}
}
@include media-breakpoint-down( md ){
	.nav__container{
		// position:static;
		#mobile-hamburger{
			display:block;
			position:absolute;
			right:20px;
			top:20px;
			font-size:40px;
			z-index:90;
			cursor:pointer;
			width: 30px;
			height: 30px;
			text-align:center;
			span{
				border-radius: 3px;
				height: 5px;
				width: 100%;
				background: $base;
				// box-shadow: 0 0 1px $white;
				position: absolute;
				left:0px;
				right:0px;
				display: inline-block;
				overflow: hidden;
				transition:all 0.3s ease;
				&:first-child,&:last-child{
					visibility:visible;
					width:100%;
					left:50%;
					transform:translateX(-50%);
				}
				&:nth-child(2){
					top: 10px;
					transform:rotate(0deg);
				}
				&:nth-child(3){
					top: 10px;
					transform:rotate(0deg);
				}
				&:last-child{
					top: 20px;
				}
			}
		}
		.header--wrap{
			position:relative;
			width:100%;
			height:100%;
		}
		nav{
			& > div {
    			min-height: 100vh;
    			padding-top: 60px;
    		}
			position:fixed;
			background:$base;
			width:100%;
			max-height:0%;
			top:0;
			left:0;
			right:0;
			// bottom:100%;
			text-align:center;
			padding-top:0px;
			z-index:80;
			visibility:hidden;
			overflow:auto;
			transition: all 1s ease;
			&::-webkit-scrollbar { 
 			   display: none; 
			}
			ul{
				width:100%;
				flex-wrap:wrap;
				li{
					width:100%;
					a{
						padding: 10px 20px;
						width:100%;
						color: $alt;
					}
				}
			}
		}
		#m_nav[type=checkbox]:checked{
			& ~ nav{
				max-height: 100%;
				// bottom:0;
				visibility:visible;
				// padding-top:$nav-bar-height;
			}
			& ~ .header--wrap{
				.fixed{
					position:fixed;
				}
				#mobile-hamburger{
					.fixed{
						width:35px;				
					}
					span{
						&:first-child,&:last-child{
							visibility:hidden;
							top:10px;
							width:0px;
						}
						&:nth-child(2){
							transform: rotate(45deg);
							background-color: $alt;
						}
						&:nth-child(3){
							transform: rotate(-45deg);
							background-color: $alt;
						}
					}
				}
			}
		}
	}
}